<template>
    <div class="web-message-name">
        <div class="left-label title-color">
            <el-tooltip :content="checkedConversationInfo.name">
                <img :src="checkedConversationInfo.avatar" />
            </el-tooltip>
            <div>{{ checkedConversationInfo.name }}</div>
        </div>
        <div class="right-icon">
            <el-tooltip content="100M">
                <div
                    v-if="checkedConversationInfo.type !== 'GROUP'"
                    @click="sendFile"
                >
                    <img
                        :src="
                            theme === 0
                                ? require('@/assets/file-icon.png')
                                : theme === 1
                                ? require('@/assets/dark-file-icon.png')
                                : ''
                        "
                    />
                </div>
            </el-tooltip>
            <el-tooltip content="">
                <div
                    v-if="checkedConversationInfo.type !== 'GROUP'"
                    @click="sendTrtc('audio')"
                >
                    <img
                        :src="
                            theme === 0
                                ? require('@/assets/audio-icon.png')
                                : theme === 1
                                ? require('@/assets/dark-audio-icon.png')
                                : ''
                        "
                    />
                </div>
            </el-tooltip>
            <el-tooltip content="">
                <div
                    v-if="checkedConversationInfo.type !== 'GROUP'"
                    @click="sendTrtc('video')"
                >
                    <img
                        :src="
                            theme === 0
                                ? require('@/assets/video-icon.png')
                                : theme === 1
                                ? require('@/assets/dark-video-icon.png')
                                : ''
                        "
                    />
                </div>
            </el-tooltip>
            <el-tooltip content="">
                <div
                    @click="$store.commit('updateShowPdf', true)"
                    v-if="
                        checkedConversationInfo.type === 'GROUP' &&
                            (userInfo.userRoleNum === 0 ||
                                userInfo.userRoleNum === 1)
                    "
                >
                    <img
                        :src="
                            theme === 0
                                ? require('@/assets/import-icon.png')
                                : theme === 1
                                ? require('@/assets/dark-import-icon.png')
                                : ''
                        "
                    />
                </div>
            </el-tooltip>
        </div>
        <input
            type="file"
            id="fileInput"
            ref="fileInput"
            style="display: none;"
            @change="getFile"
        />
    </div>
</template>
<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState({
            userInfo: (state) => state.userInfo,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            trtcInfo: (state) => state.trtcInfo,
            theme: (state) => state.theme,
        }),
    },
    watch: {
        checkedConversationInfo: {
            handler(val) {
                console.log("", val);
            },
            deep: true,
            immediate: true,
        },
        userInfo: {
            handler(val) {
                console.log("", val);
            },
            deep: true,
            immediate: true,
        },
    },
    data() {
        return {
            dialogVisible: false,
            dateTime: [],
        };
    },
    methods: {
        async sendTrtc(type) {
            console.log(
                "***********************************testRes********************************",
                this.trtcInfo
            );
            if (this.trtcInfo.localStream) return;
            // 
            if (!this.trtcInfo.browserSupport) {
                return this.messageFun(
                    "",
                    "error",
                    true,
                    "<b>QQ360</b>"
                );
            }
            switch (type) {
                case "audio":
                    if (!this.trtcInfo.isMicrophone) {
                        return this.messageFun(
                            "",
                            "error",
                            true,
                            "<b></b>"
                        );
                    }
                    break;
                case "video":
                    if (!this.trtcInfo.isCamera) {
                        return this.messageFun(
                            "",
                            "error",
                            true,
                            "<b></b>"
                        );
                    }
                    break;
            }
            console.log("===========================>");
            // 
            await this.$store.dispatch("createNative", type);
            if (type === "audio") {
                this.trtcInfo.localStream.setAudioProfile("high");
            } else {
                this.trtcInfo.localStream.setVideoProfile("4K");
            }
            // 
            let roomId = parseInt(Math.random() * 1000000);
            this.$store.commit("updateTrtc", {
                roomIdAndType: {
                    roomId,
                    type,
                    id: this.checkedConversationInfo.userOrGroupId,
                    name: this.checkedConversationInfo.name,
                },
            });
            console.log(this.trtcInfo);
            // 
            this.$store.dispatch("joinRoom").then(() => {
                console.log(
                    "==================================>"
                );
                this.sendCustomMessage(type, roomId);
                this.$store.commit("updateTrtc", {
                    isShowComponent: true,
                    type: "WITH_ANSWER",
                    subTitle: `${this.checkedConversationInfo.name}`,
                });
            });
        },
        // 
        sendFile() {
            console.log("");
            this.$refs.fileInput.click();
        },
        getFile(event) {
            console.log("--------------------->", event);
            let file = event.target.files[0];
            // 100M
            let size = file.size / 1024 / 1024;
            console.log(file, size);
            if (size > 100) {
                return this.messageFun(
                    "",
                    "warning",
                    true,
                    "<b>100M</b>"
                );
            }
            const h = this.$createElement;
            this.$msgbox({
                title: "",
                message: h("div", { style: "font-weight: bold" }, [
                    h("div", null, `${file.name}`),
                    h(
                        "div",
                        null,
                        `${
                            file.size < 1024
                                ? file.size + ""
                                : file.size / 1024 < 1024
                                ? Math.round(file.size / 1024) + "K"
                                : Math.round(file.size / 1024 / 1024) + "M"
                        }`
                    ),
                ]),
                showCancelButton: true,
                confirmButtonText: "",
                cancelButtonText: "",
                beforeClose: (action, instance, done) => {
                    console.log(
                        "===========================>",
                        this.$parent.selfAvatar
                    );
                    if (action === "confirm") {
                        instance.confirmButtonLoading = true;
                        instance.confirmButtonText = "";
                        let message = this.$tim.createFileMessage({
                            to: this.checkedConversationInfo.userOrGroupId,
                            conversationType: this.TIM.TYPES.CONV_C2C,
                            payload: {
                                file: document.getElementById("fileInput"),
                            },
                            onProgress: function(event) {
                                console.log("file uploading:", event);
                                instance.confirmButtonText =
                                    " " +
                                    event * 100 +
                                    " %";
                            },
                        });
                        console.log(message);
                        this.$tim.sendMessage(message).then(async (res) => {
                            let msgRes = res.data.message;
                            instance.confirmButtonLoading = false;
                            msgRes.avatar = this.$parent.selfAvatar;
                            this.$parent.messageList = this.$parent.messageList.concat(
                                [msgRes]
                            );
                            console.log(
                                "==========================>",
                                msgRes,
                                this.$parent.selfAvatar,
                                this.$parent.messageList
                            );
                            done();
                        });
                    } else {
                        console.log("");
                        if (instance.confirmButtonLoading === true) return;
                        done();
                    }
                },
            }).catch(() => {});
        },
        // 
        messageFun(title, type, isDangerouslyUseHTMLString = true, message) {
            this.$notify({
                type: type,
                title: title,
                dangerouslyUseHTMLString: isDangerouslyUseHTMLString,
                duration: 3000,
                message,
            });
        },
        // 
        async sendCustomMessage(type, roomId) {
            let userRes = await this.$parent.getMemberInfo([
                this.userInfo.userID,
            ]);
            console.log(
                "==================================",
                userRes
            );
            let message = this.$tim.createCustomMessage({
                to: this.checkedConversationInfo.userOrGroupId,
                conversationType: this.TIM.TYPES.CONV_C2C,
                payload: {
                    data: "AUDIO_OR_VIDEO",
                    description: JSON.stringify({
                        roomId,
                        type,
                        id: userRes.data.userID,
                        name: userRes.data.nick,
                    }),
                    extension: `${
                        type === "audio" ? "" : ""
                    }`,
                },
            });
            let sendRes = await this.$tim.sendMessage(message);
            sendRes.data.message.nick = userRes.data.nick;
            sendRes.data.message.avatar =
                userRes.data.profileCustomField[0].value === "0"
                    ? this.theme === 0
                        ? require("@/assets/wuman-icon.png")
                        : require("@/assets/dark-wuman-icon.png")
                    : this.theme === 0
                    ? require("@/assets/man-icon.png")
                    : require("@/assets/dark-man-icon.png");
            this.$set(
                sendRes.data.message.payload,
                "text",
                sendRes.data.message.payload.extension
            );
            this.$parent.messageList = this.$parent.messageList.concat([
                sendRes.data.message,
            ]);
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-message-name.less";
</style>
